<template>
	<v-card>
		<v-card-title class="py-2">
			Edit Your Profile Incubator
		</v-card-title>
		<v-form v-model="valid" ref="form" enctype="multipart/form-data">
			<v-card-text>
				<v-text-field
          v-model="dataIncubator.name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.profile.lokasi"
          label="Location"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.profile.phone"
          label="Phone"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.email"
          label="E-mail"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.profile.website"
          label="Website"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataIncubator.profile.instagram"
          label="Instagram"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>
        <v-text-field
          v-model="dataIncubator.profile.facebook"
          label="Facebook"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          v-model="dataIncubator.profile.youtube"
          label="Youtube"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field>  
        <v-text-field
          v-model="dataIncubator.profile.since"
          label="Since"
          dense
          :rules="dateRule"
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          label="Total Startup"
          dense
          autocomplete="off"
        ></v-text-field> 
        <v-text-field
          label="Total Mentor"
          dense
          autocomplete="off"
        ></v-text-field> 
        <img 
          v-if="dataIncubator.profile.logo !== null"
          class="mt-3" 
          :src="`http://progresio.id/api/public/uploads/logo/${dataIncubator.profile.logo}`" 
          alt="logo incubator"
          width="200" /> 
        <v-avatar 
          v-else
          size="80"
          color="grey lighten-3">
          Logo
        </v-avatar>
        <v-file-input
          accept="image/*"
          v-model="logo"
          show-size
          counter
          label="Logo"
        ></v-file-input> 
        <v-textarea
          v-model="dataIncubator.profile.about"
          label="About"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-textarea>  
        <div class="title mb-3">Profil Manager</div>                 
        <v-text-field
          v-model="dataIncubator.profile.manager_name"
          label="Name"
          dense
          :rules="requiredRule"
          autocomplete="off"
        ></v-text-field> 
        <img 
          v-if="dataIncubator.profile.manager_profile_foto !== null"
          class="mt-3" 
          :src="`http://progresio.id/api/public/uploads/profile/${dataIncubator.profile.manager_profile_foto}`"
          alt="photo manager"
          width="200" /> 
        <v-avatar 
          v-else
          size="80"
          color="grey lighten-3">
          Photo
        </v-avatar>          
        <v-file-input
          accept="image/*"
          v-model="photo"
          show-size
          counter
          label="Photo"
        ></v-file-input>

        <Notif :msg="msg" :status="status" @visible="visible" />
			</v-card-text>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn
          color="primary"
          text
          @click="$router.go(-1)">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          :loading="isLoading"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
			</v-card-actions>
		</v-form>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

	export default{
		data: () => ({
			valid: true,
      photo: null,
      logo: null,
      requiredRule: [v => !!v || 'Data harus diisi'],
      msg: {
        success: 'Berhasil diubah',
        error: 'Gagal diubah',
        visible: false
      }
		}),

    components: {
      Notif
    },    

		computed: {
      ...mapState('dataIncubatorIncubator', ['dataIncubator', 'isLoading', 'status']),
      dateRule () {
        return [
          ...this.requiredRule,
          v => !!v.match(/^(19[5-9][0-9]|20[0-4][0-9]|2050)$/igm) || 'Harus berupa tahun'
        ]
      }
    },

		methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = new FormData()
            data.append('name', this.dataIncubator.name)
            data.append('lokasi', this.dataIncubator.profile.lokasi)
            data.append('phone', this.dataIncubator.profile.phone)
            data.append('website', this.dataIncubator.profile.website)
            data.append('instagram', this.dataIncubator.profile.instagram)
            data.append('facebook', this.dataIncubator.profile.facebook)
            data.append('youtube', this.dataIncubator.profile.youtube)
            data.append('since', this.dataIncubator.profile.since)
            data.append('about', this.dataIncubator.profile.about)
            data.append('logo', this.logo)
            data.append('manager_name', this.dataIncubator.profile.manager_name)
            data.append('profile_foto', this.photo)
            data.append('email', this.dataIncubator.email)
          // console.log(data)             
          this.$store.dispatch('dataIncubatorIncubator/postDataIncubator', data)
          this.$store.dispatch('dataIncubatorIncubator/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
        this.$store.commit('dataIncubatorIncubator/updateStatus', null)
      }
    }
	}
</script>